<template>
    <v-container class="container">
      <v-row>
        <v-col col="12" sm="12" offset-md="1" order-md="1" md="10" offset-lg="2" order-lg="2" lg="8" offset-xl="3" order-xl="3" xl="6">
          <v-card class="ma-auto mt-16 pa-10">
            <v-card-title class="text-h5 justify-center">Registrarme</v-card-title>
            <v-divider></v-divider>
            <v-form v-model="formValue">
              <v-text-field
                label="email"
                v-model="email"
                :rules="emailRules"
              ></v-text-field>
              <v-text-field
                label="username"
                v-model="username"
                :rules="inputRules"
              ></v-text-field>
              <v-text-field
                label="contraseña"
                v-model="password"
                :rules="inputRules"
                type="password"
              ></v-text-field>
              <v-text-field
                label="contraseña"
                v-model="passwordCompare"
                :rules="passwordRules"
                type="password"
                v-on:keyup.enter="register"
              ></v-text-field>
              <v-card-actions>
                <v-btn block color="primary" :loading="registerLoad" @click="register">Registrarme</v-btn>
              </v-card-actions>
              <v-divider></v-divider>
              <v-card-text class="text-h6 text-center">¿Ya tienes cuenta?</v-card-text>
              <v-card-actions>
                <v-btn to="/login" block color="secondary">Iniciar Sesión</v-btn>
              </v-card-actions>
            </v-form>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </template>
  
  <script>
  import validator from 'validator'
  import userProvider from '../providers/UserProvider'


  export default {
    data(){
      return{
        registerLoad: false,
        formValue: false,
        email: '',
        username: '',
        password: '',
        passwordCompare: '',
        inputRules: [value => !!value || 'Este campo no puede estar vacío.'],
        emailRules: [
            value => !!value || 'Este campo no puede estar vacío.',
            value => validator.isEmail(value) || 'Debe contener un email valido.'
        ]
      }
    },
    computed:{
        passwordRules(){
            return [
                value => this.password === value || 'Las contraseñas deben ser iguales.'
            ]
        }
    },
    methods:{
      async register(){
        if(this.formValue){
          this.registerLoad = true
          try{
            await userProvider.createUser(this.username, this.password, this.email)
            this.registerLoad = false
            this.$router.push('/login')
            console.log('Se registró con exito.') //TODO alert
          }catch(error){
            console.log(error) //TODO alert
          }
        }
      }
    }
  }
  </script>
  
  <style scoped>
    .container{
      height: 100vh;
    }
  </style>